.container-olvido {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
    background-color: rgb(252, 252, 252);
}

.container-register-contenido {
    width: 40%;
    padding: 20px;
    text-align: center;
    display: block;
    position: absolute;
}

.cont-password {
    display: flex;
    margin-left: 65px;
}

input {
    width: 70%;
    height: 40px;
    margin-bottom: 20px;
    padding: 10px;
    outline: none;
}
.input-pass{
    width: 92.5%;
    margin-left: 20px;
}
.logo-reset-pass {
    width: 500px;
    height: 190px;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    margin-bottom: 50px;
}

.logo-mkdpt-register {
    left: 36%;
    top: 4.5%;
    position: absolute;
    width: 180px;
    height: 70px;
    z-index: 2;
}

.title-reset-pass {
    position: absolute;
    color: #ffff;
    z-index: 3;
    left: 22%;
    top: 36%;
    text-shadow:
        1px 1px 0 #000;
}

#mensaje {
    color: red;
    padding: 0;
    margin: 0;
}

.cont-registrarse {
    display: flex;
    justify-content: left;
    margin-left: 85px;
    width: 40%;
}

.cont-registrarse p {
    align-content: flex-start;
}

.cont-registrarse a {
    color: inherit;
}

.cont-registrarse p:visited {
    color: inherit;
}
.texto-reset-pass{
    width: 70%;
    font-size: 10px;
    color: #afaeae;
    margin-left: 88px;
    margin-bottom: 20px;
    text-align: left;
}
#user-name-reset{
    margin: 10px; 
}

.enlace-volver{
    margin-top: 15px;
    margin-left: 20px;
    font-size: 14px;
}
